<template>
  <div class="search">
    <van-nav-bar title="商品搜索" left-arrow @click-left="$router.go(-1)" />

    <van-search v-model="userSearch" show-action placeholder="请输入搜索关键词" clearable>
      <template #action>
        <div @click="gosearch(userSearch)">搜索</div>
      </template>
    </van-search>

    <!-- 搜索历史 -->
    <div class="search-history" v-if="history.length>0">
      <div class="title">
        <span>最近搜索</span>
        <van-icon name="delete-o" size="16" @click="clear" />
      </div>
      <div class="list" >
        <div class="list-item" @click="gosearch(item)" v-for="item in history" :key="item">{{ item }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import {setHistorylist,getHistoryList}  from '@/utils/storage.js'
export default {
  name: 'SearchIndex',
  data(){
    return{
      history:getHistoryList()||[] ,

      userSearch:""
    }
  },

  methods:{ 
    gosearch(value){

      // 判断一下用户输入的是否是一个空的字符串
      if(value){

        // 判断用户输入的内容有没有在数组中
       const hisNum= this.history.indexOf(value)
        if(hisNum===-1){
          this.history.unshift(value)

          setHistorylist(this.history)
           // 输入框内容清空
          this.userSearch=''


          //跳转到商品列表页
          this.$router.push(`/searchList?search=${value}`)
        }else{
          // 将数组中对应的元素删除
          this.history.splice(hisNum,1)
          // 将元素添加进去
          this.history.unshift(value)

          setHistorylist(this.history)
           // 输入框内容清空
          this.userSearch=''

          this.$router.push(`/searchList?search=${value}`)


        }    
      }
      
    },

    clear(){
      this.history=[]
    }
  }
}
</script>

<style lang="less" scoped>
.search {
  .searchBtn {
    background-color: #fa2209;
    color: #fff;
  }
  ::v-deep .van-search__action {
    background-color: #c21401;
    color: #fff;
    padding: 0 20px;
    border-radius: 0 5px 5px 0;
    margin-right: 10px;
  }
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  .title {
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
  }
  .list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    padding: 0 10px;
    gap: 5%;
  }
  .list-item {
    width: 30%;
    text-align: center;
    padding: 7px;
    line-height: 15px;
    border-radius: 50px;
    background: #fff;
    font-size: 13px;
    border: 1px solid #efefef;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-bottom: 10px;
  }
}
</style>